.card {
    margin: 20px;
    text-align: center;
    width: 250px;
    border-radius: 5px;
    box-shadow: 0px 5px 10px hsl(0, 0%, 80%);
    overflow: hidden;
    background-color: blanchedalmond;
  }
  .image {
    width: 100%;
    height: 50%;
  }
  input[type="radio"] {
    display: none;
  }
  .btn {
    font-weight: 600;
    margin: 20px;
    border: 1px solid hsl(0, 0%, 0%);
    background-color: hsl(0, 0%, 100%);
    border-radius: 5px;
    padding: 10px 10px 10px 0px;
  }
  .btn:hover {
    color: hsl(0, 0%, 100%);
    background-color: hsl(0, 0%, 0%);
  }
  .icon {
    padding: 10px;
  }
  .btn2{
    cursor: pointer;
  }